.text{
    margin: 10px 0;
    width: 21ch;
    font-family: monospace;
    white-space: nowrap;
    overflow: hidden;
    border-right: 2px solid white;
    animation: border .4s step-start infinite,
                 text 6s steps(21) infinite;
}
@keyframes border {
    0%{
    border-right: 2px solid #cfcfcf
}
    50%{
        border-right: 2px solid transparent;
    }
    100%{
        border-right: 2px solid white;
    }
}
@keyframes text {
    from{
        width: 0;
    }
    to{
        width: 21ch;
    }

}
.social{
    height: 150px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px auto;
}
